import React,{useEffect} from "react";
import "./dgc.scss";
import bus from "./../../../assets/images/bus.png"
import room from "./../../../assets/images/room.png"

const Dgc = () => {
    return(
        <div id="digital-career-center">
            <nav></nav>
            <div className="dgc-content">
                <div className="dgc-title-back-layer">
                    <h2>Digital Career center</h2>
                </div>
                <div className="dgc-content-wrapper">
                <div className="dgc-image-content">
                    <div className="image-wrapper">
                        <img src={room} alt=""/>
                    </div>
                    <div className="image-wrapper">
                        <img src={bus} alt=""/>
                    </div>
                  
                </div>
                <div className="dgc-text-content">
                <p>Nous croyons fermement que la technologie est laffaire de tous. Cest une rvolution, une culture, des comptences que tous citoyens en ge et capacit dapprendre se doit dacqurir. Cest pourquoi nous avons  cur de contribuer au dveloppement de tous, en ce qui concerne les domaines du numrique notamment  travers lducation et le coaching professionnel.</p>
                </div>

                </div>
            <footer>
                <div className="footer-wrapper">
                    <h3>IT</h3>
                    <h3>TECHSPACE</h3>
                    <h3>CODE</h3>
                    <h3>DIGITAL</h3>
                    <h3>ATELIER</h3>
                    <h3>GIDITAL</h3>

                </div>
                   

            </footer>
            </div>
        </div>
    )
}

export default Dgc